<template>
	<div class="contract-details">

		<template v-if="!store.state.contract">
			<ContractDataPreloader :hasInfoMessage="contractRemainDays >= 10">
				<template v-if="contractRemainDays >= 10" v-slot:contract-info-msg>
					<ContractInfoMessagePreloader class="contract-details__contract-info-message"/>
				</template>
			</ContractDataPreloader>
		</template>

		<template v-else>
			<ContractData :hasInfoMessage="contractRemainDays >= 10" :contract="store.state.contract" :promos="store.state.availablePromos">
				<template v-if="contractRemainDays >= 10" v-slot:contract-info-msg>
					<ContractInfoMessage :contractRemainDays="contractRemainDays" :contract="store.state.contract" class="contract-details__contract-info-message"/>
				</template>
			</ContractData>
		</template>

	</div>
</template>
