import ContractData from '@/entities/components/ContractData/ContractData.vue';
import ContractInfoMessage from '@/entities/components/ContractInfoMessage/ContractInfoMessage.vue';
import ContractDataPreloader from '@/entities/components/Preloaders/ContractData';
