const contractRemainDays = computed(() => {
	const timeDiff = Math.abs(new Date(store.state.contract?.datereturn).getTime() - new Date().getTime());
	const diffDays = Math.ceil(timeDiff / (1000 * 60 * 60 * 24));
	console.log('diffDays', diffDays);

	return diffDays;
});

