<template>
	<div class="contract-info-message">
		<h2 class="contract-info-message__headline">Информационное сообщение</h2>
		<p class="contract-info-message__text">До окончания действия договора - {{contractRemainDays}} {{ declDays(contractRemainDays) }} до {{ contractDayMonth }}</p>
		<ProgressBar class="contract-info-message__progress-bar" :total="contract.returnperiod" :remain="contractRemainDays"/>
		<Button class="button--contract-info-message">
			<span class="button--contract-info-message__icon"></span>
			<span class="button--contract-info-message__text">Продлите договор, оплатив  процентную часть в размере {{useLocalSum(contract.percents_sum) }} р.</span>
			<span class="button--contract-info-message__right"></span>
		</Button>
	</div>
</template>
<script setup>
import ProgressBar from '@/shared/components/ProgressBar';
import Button from '@/shared/components/Button';
import { computed, defineProps } from 'vue';
import useLocalSum from '@/shared/lib/hooks/useLocalSum';
import { declDays } from '@/shared/lib/helpers/declension';

const props = defineProps({
	contract: {
		type: Object,
		required: true,
	},
	contractRemainDays: {
		type: Number,
		required: true,
	},
});

const contractDayMonth = computed(() => {
	const current = new Date(props.contract.datereturn).toLocaleString('ru', {
		month: 'long',
		day: 'numeric',
	}).split(' ');

	current.splice(3, 1);
	return current.join(' ');
});

console.log(useLocalSum(props.contract.percents_sum));
</script>
<style lang="scss">
@import './contract-info-message.scss';
</style>
