<template>
	<div class="contract-data">
		<h1 class="contract-data__headline" :class="{'sm' : hasInfoMessage}">Договор №{{contract.number}} от
			{{ transformDate(contract.created_at) }}
		</h1>
		<p v-if="hasInfoMessage" class="contract-data__additional-info">Дополнительный информационный текст дополнительный информационный текст</p>

		<slot name="contract-info-msg"></slot>

		<div class="contract-data__backsum contract-info-block">
			<h2 class="contract-info-block__headline">Сумма к возврату</h2>
			<div class="contract-info-block__data-wrap">
				<div class="contract-info-block__item">
					<span class="contract-info-block__label">Всего начислено</span>
					<span class="contract-info-block__number">{{ useLocalSum(contract.total_debt) }} p.</span>
				</div>
				<div class="contract-info-block__item">
					<span class="contract-info-block__label">Основной долг</span>
					<span class="contract-info-block__number">{{ useLocalSum(contract.main_debt) }} p.</span>
				</div>
				<div class="contract-info-block__item">
					<span class="contract-info-block__label">Начислено процентов</span>
					<span class="contract-info-block__number">{{ useLocalSum(contract.percents_sum) }} p.</span>
				</div>
			</div>
			<div class="contract-info-block__end-date">
				<span class="contract-info-block__end-date-label">Необходимо вернуть до</span>
				<span class="contract-info-block__end-date-day">{{ transformDate(contract.datereturn) }}</span>
			</div>
		</div>

		<Button
			:handler="ClickBtnCustom"
			:isPreloader="true"
			class="button--contract-data contract-data__pay-btn">
			Перейти к оплате
		</Button>

		<div class="contract-data__conditions contract-info-block">
			<h2 class="contract-info-block__headline">Условия</h2>
			<div class="contract-info-block__data-wrap">
				<div class="contract-info-block__item">
					<span class="contract-info-block__label">Процентная ставка</span>
					<span class="contract-info-block__number">{{ contract.rates[0].rate }} %</span>
				</div>
				<div class="contract-info-block__item">
					<span class="contract-info-block__label">В рублях</span>
					<span class="contract-info-block__number">{{useLocalSum(debtSumByPercent)}} р.</span>
				</div>
				<div class="contract-info-block__item">
					<span class="contract-info-block__label">Примененные акции</span>
					<ul style="display: flex; flex-direction: column">
						<li style="margin-bottom: 3px" class="contract-info-block__number" v-for="promo in promos" :key="promo">
							{{promo.title}}
						</li>
					</ul>
<!--					<span class="contract-info-block__number">{{props.promos[0]?.title}}</span>-->
				</div>
			</div>
			<div class="contract-info-block__end-date">
				<span class="contract-info-block__end-date-label">Беспроцентный период</span>
				<!-- <span class="contract-conditions__end-date-day">18 февраля 2023</span> -->
			</div>
		</div>
	</div>
</template>
<script setup>
import Button from '@/shared/components/Button';
import { computed, defineProps } from 'vue';
import useLocalSum from '@/shared/lib/hooks/useLocalSum';

const props = defineProps({
	hasInfoMessage: {
		type: Boolean,
		default: false,
	},
	contract: {
		type: Object,
		required: true,
	},
	promos: {
		type: Array,
		required: true,
	},
});

function transformDate(date) {
	const current = new Date(date).toLocaleString('ru', {
		month: 'long',
		day: 'numeric',
		year: 'numeric',
	}).split(' ');

	current.splice(3, 1);
	return current.join(' ');
}

const debtSumByPercent = computed(() => (Math.round(props.contract.main_debt / 100) * props.contract.rates[0].rate));

const ClickBtnCustom = async () => {
	console.log('ClickBtnCustom');
	return new Promise((resolve) => setTimeout(resolve, 2000));
};
</script>
<style lang="scss">
@import './contract-data.scss';
</style>
