<template>
	<div class="progress-bar">
		<div class="progress-bar__front" :style="barWidth"></div>
	</div>
</template>

<script setup>
import { computed, defineProps } from 'vue';

const props = defineProps({
	days: {
		type: Number,
		default: 90,
	},
	total: {
		type: Number,
		default: 35,
	},
	remain: {
		type: Number,
		default: 10,
	},
});

const barWidth = computed(() => {
	const width = Math.round(100 - (props.remain * 100) / props.total);

	return {
		width: `${width}%`,
	};
});
</script>

<style lang="scss">
@import './progress-bar';
</style>
