// УНИВЕРСАЛЬНАЯ ФУНКЦИЯ СКЛОНЕНИЯ
function setDeclension(number, titles) {
    const cases = [2, 0, 1, 1, 1, 2];

    return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
}

export const declDepartments = (v) => setDeclension(v, ['отделение', 'отделения', 'отделений']);
export const declDays = (v) => setDeclension(v, ['день', 'дня', 'дней']);
export const declHours = (v) => setDeclension(v, ['час', 'часа', 'часов']);
export const declMinutes = (v) => setDeclension(v, ['минута', 'минуты', 'минут']);
export const declSeconds = (v) => setDeclension(v, ['секунда', 'секунды', 'секунд']);

